  import React from 'react';
import { ThemeProvider } from 'styled-components';
import { storiesOf } from '@storybook/react';
import { FontAwesomeIcon as Icon } from '@fortawesome/react-fontawesome';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons';

import { ThemeStyle } from '../../../styles';

import Select from '../index';
import SelectReadme from './Select.readme.md';

const options = [{ value: 'chocolate', label: 'Chocolate' }, { value: 'strawberry', label: 'Strawberry' }, { value: 'vanilla', label: 'Vanilla' }];
storiesOf('Forms|Select', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__))
  .addParameters({
    readme: {
      sidebar: SelectReadme,
    },
  })
  .add('Default', () => (
    <ThemeProvider theme={ThemeStyle}>
      <Select options={options} />
    </ThemeProvider>
  ))
  .add('With Error State', () => (
    <ThemeProvider theme={ThemeStyle}>
      <Select options={options} isError />
    </ThemeProvider>
  ))
  .add('With Disabled State', () => (
    <ThemeProvider theme={ThemeStyle}>
      <Select options={options} value={options[0]} isDisabled />
    </ThemeProvider>
  ))
  .add('With Custom Dropdown Indicator', () => (
    <ThemeProvider theme={ThemeStyle}>
      <Select dropdownIndicator={() => <Icon icon={faCalendarAlt} />} value={options[0]} options={options} />
    </ThemeProvider>
  ));

  